<template>
    <div class="layout-px-spacing">  
            <ul class="navbar-nav flex-row mt-2">
                <li>
                    <div class="page-header">
                        <nav class="breadcrumb-one" aria-label="breadcrumb">
                            <ol class="breadcrumb">
                                <li class="breadcrumb-item"><a href="javascript:;">Masters</a></li>
                                <li class="breadcrumb-item active" aria-current="page"><span>Requisitions</span></li>
                            </ol>
                        </nav>
                    </div>
                </li>
            </ul>

            <div class="d-flex me-4 mt-1">
                        <div class="ms-auto">
              <button v-if="!requisition_creation" type="button" class="btn btn-primary mb-2"  @click="requisition_creation = true">
        <template v-if="s && loading_spinner">
                    <span class="spinner-border spinner-border-sm text-success ms-2" role="status" aria-hidden="true"></span>
                </template>
                <template v-else>
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-plus-square"><rect x="3" y="3" width="18" height="18" rx="2" ry="2"></rect><line x1="12" y1="8" x2="12" y2="16"></line><line x1="8" y1="12" x2="16" y2="12"></line></svg>
                </template>
     </button>
     <!-- <button v-else type="button" class="btn btn-danger mb-2"  @click="requisition_creation = false">
      X
     </button> -->
 
                        </div>
                    </div>  


    <!-- requisition registration form starts -->
    <form v-if="requisition_creation" class="p-3 border rounded bg-white shadow" @submit.prevent="registerRequisition" id="close">
        <h4 class="text-center">REQUISITION REGISTRATION FORM</h4>
        <div class="row mb-2">
            
                  <div class="col-md-3">
                <label class="form-label">Select Requisition Type </label>
       <multiselect 
                    v-model="requisition_type"
                    :options= "[`${route.params.type}`]"
                    placeholder="Select Requisition Type"
                    :searchable = "true"        
                    multple="false"
                    :required="true"
                    :allow-empty="false"
                    >
                </multiselect>
            </div>
              
            <div 
             v-if="requisition_type"
            class="col-md-2">
                <label>Requisition Date</label>
             <input 
  type="date"
  class="form-control"
  v-model="date"
  :min="new Date().toISOString().split('T')[0]"
>

            </div>

            <div 
             v-if="date"
            class="col-md-2">
                <label>Required Date</label>
               <input
                 type="date"
                 class="form-control"
                 v-model="required_date"
                 :min="minRequiredDate"
               >
            </div>
            
              <div 
             v-if="required_date"
            class="col-md-2">
              <label>Proforma (PDF only)</label>
               <input
               type="file"
               class="form-control"
               @change="handleFileUpload"
               accept="application/pdf"
               >
            </div>
             <div 
              v-if="required_date"
             class="col-md-3">
              <label>Select Vendor</label>
            <multiselect 
                    v-model="vendor"
                    :options= "vendors"
                    placeholder="Select Vendor"
                    label="name"
                    :searchable = "true"        
                    multple="false"
                    :required="true"
                    :allow-empty="false"
                    >
                </multiselect>
            </div>

<div v-if="required_date" class="col-md-1">
  <label class="form-label">Asset? </label>
  <div class="border rounded px-2 py-2 d-flex align-items-center">
    <input 
      type="checkbox" 
      class="form-check-input me-2"
      v-model="asset_required"
    >
    <span>{{ asset_required ? 'Yes' : 'No' }}</span>
  </div>
</div>

<div v-if="required_date" class="col-md-1 me-2">
  <label class="form-label">Subcontractor? </label>
  <div class="border rounded px-2 py-2 d-flex align-items-center">
    <input 
      type="checkbox" 
      class="form-check-input me-2"
      v-model="subcontractor"
    >
    <span>{{ subcontractor ? 'Yes' : 'No' }}</span>
  </div>
</div>


   <div 
          v-if="subcontractor" 
            class="col-md-4 me-2">
                <label>Select Subcontraactor</label>
                <multiselect 
                    v-model="selected_subcontractor"
                    :options= "subcontractors"
                    placeholder="Select Subcontractor"
                    label="name"
                    :searchable = "true"        
                    multple="false"
                    :required="true"
                    :allow-empty="true"
                    >
                </multiselect>
            </div>

            
             <div 
             v-if="asset_required"
            class="col-md-3">
                <label>Select An asset</label>
                <multiselect 
                    v-model="asset"
                    :options= "assets"
                    placeholder="Select From List"
                    label="name"
                    :searchable = "true"        
                    multple="false"
                    :required="true"
                    :allow-empty="true"
                    >
                </multiselect>
            </div>
            

<div
v-if="required_date"
class="row"
>
              <div
               v-if="requisition_type === 'budget'"
               class="col-md-3">
              <label>Select Item </label>
                <multiselect 
                    v-model="item"
                    :options= "budgetItems"
                    placeholder="Select From List"
                    label="name"
                    :searchable = "true"        
                    multple="false"
                    :required="true"
                    :allow-empty="false"
                    >
                </multiselect>
            </div>
            <div 
            v-else
            class="col-md-3">
              <label>Enter Item</label>
               <input type="text" class="form-control" v-model="item" placeholder="Enter Item">
            </div>
              <div
               v-if="requisition_type === 'budget'"
               class="col-md-4">
              <label>Enter Description</label>
                <multiselect 
                    v-model="description"
                    :options= "budgetItemDescriptions"
                    placeholder="Select From List"
                    label="name"
                    :searchable = "true"        
                    multple="false"
                    :required="true"
                    :allow-empty="false"
                    >
                </multiselect>
            </div>
            <div
            v-else
            class="col-md-3">
              <label>Enter Description</label>
               <input type="text" class="form-control" v-model="description" placeholder="Enter Description">
            </div>

             <div class="col-md-2"
               v-if="requisition_type === 'budget' "
             >
               <label>Specification </label>
               <input type="text" class="form-control" v-model="specification" placeholder="Enter Specification">
            </div>
             <div class="col-md-2">
              <label>Quantity</label>
<input type="number" 
       class="form-control" 
       v-model="quantity" 
       :placeholder="'Available '+max_quantity" 
       step="any" 
       :max="max_quantity"
       @input="enforceMax()">
            </div>
            <div class="col-md-2">
              <label>Enter Rate   </label>
               <input type="number" class="form-control" v-model="rate" :placeholder=" 'Budgeted  : '+ available_rate" step="any"
                @input="enforceMax()"
               >
            </div>

              <div class="col-md-2">
              <label>Total Cost </label>
                     <label class="form-control"  >{{ formatNumber(quantity * rate) }}</label>
            </div>

             <div class="col-md-1"
               v-if="requisition_type === 'budget' "
             >
             <span
             v-if="description"
             >
               <label>Unit</label>
               <label class="form-control"  >{{ units }}</label>
             </span>
            
            </div>
             <div
             v-else
             class="col-md-2">
              <label>Unit</label>
            <multiselect 
                    v-model="units"
                    :options= "unitList"
                    placeholder="Select Unit"
                    label="short_name"
                    :searchable = "true"        
                    multple="false"
                    :required="true"
                    :allow-empty="false"
                    >
                </multiselect>
            </div>
             <div class="col-md-1">
              <button type="button" class="btn btn-outline-success btn-sm p-1 mt-4"
              @click="pushItem()"
              >
               <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-plus-square"><rect x="3" y="3" width="18" height="18" rx="2" ry="2"></rect><line x1="12" y1="8" x2="12" y2="16"></line><line x1="8" y1="12" x2="16" y2="12"></line></svg>
            </button>
            </div>
</div>

               <div 
               v-if="formRequisitionItems.length>0"
               class="row layout-top-spacing">
            <div class="col-xl-12 col-lg-12 col-sm-12 layout-spacing">
                <div class="panel p-0">
                    <div class="custom-table table3">
                        <v-client-table :data="formRequisitionItems" :columns="formItemColumns" :options="table_option" >
                          <template #actions="props">
 <a href="javascript:void(0);" title="Delete" @click="pullItem(props.row.Sno)">
                                    <svg
                                        xmlns="http://www.w3.org/2000/svg"
                                        width="24"
                                        height="24"
                                        viewBox="0 0 24 24"
                                        fill="none"
                                        stroke="currentColor"
                                        stroke-width="1.5"
                                        stroke-linecap="round"
                                        stroke-linejoin="round"
                                        class="text-danger"
                                    >
                                        <polyline points="3 6 5 6 21 6"></polyline>
                                        <path d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"></path>
                                    </svg>
                                </a>
                          </template>
                        </v-client-table>

                    </div>
                </div>
            </div>
        </div>
            <!-- requisition items ends -->
          
        </div>
        <div class="d-flex">
            <div class="ms-auto gap-2 justify-content-end">
            <button type="button" class="btn btn-secondary me-3" @click="requisition_creation = false,clearForm()" id="cancel">View Requisitions</button>
            <button type="button" class="btn btn-danger me-2" @click="$router.go(-1)">Cancel</button>    
            <button type="submit" class="btn btn-primary" :disabled="loading_spinner || formRequisitionItems.length<=0 || loading ">
                <span v-if="loading" class="spinner-border spinner-border-sm me-1" role="status" aria-hidden="true"></span>
                <span v-else> Register</span>
            </button>
        </div>
        </div>
    </form>
             <!-- requisition registration form ends-->
 
                           <!-- loading spinner starts -->
                 <div v-if="loading_spinner " v-html="loadingIndicator()"></div>
        <!-- Loading spinner ends -->

            <!-- requisition table starts -->
        <div v-if="!requisition_creation" class="row layout-top-spacing">
            <div class="col-xl-12 col-lg-12 col-sm-12 layout-spacing">
                <div class="panel p-0">
                    <div class="custom-table table3">
                        <v-client-table :data="requisitions" :columns="columns" :options="table_option" >
                            <template #status="props">
                                <span class="badge inv-status" :class="'badge-' + props.row.status.class">{{ props.row.status.key }} </span>
                            </template>
                            <template #actions="props">

                            
                                 
                                 <a href="javascript:void(0);" title="View" data-bs-toggle="modal" data-bs-target="#view_requisition"
                                
                                 @click="fillItems(props.row)"
                                 >
                                    <svg
                                        xmlns="http://www.w3.org/2000/svg"
                                        width="24"
                                        height="24"
                                        viewBox="0 0 24 24"
                                        fill="none"
                                        stroke="currentColor"
                                        stroke-width="1.5"
                                        stroke-linecap="round"
                                        stroke-linejoin="round"
                                        class="text-info"
                                        data-bs-toggle="modal" data-bs-target="#requisitionItems"
                                        
                                    >
                                        <path d="M1 12s4-8 11-8 11 8 11 8-4 8-11 8-11-8-11-8z"></path>
                                        <circle cx="12" cy="12" r="3"></circle>
                                    </svg>
                                </a>
                
                            </template>
                           
                        </v-client-table>
                    </div>
                </div>
            </div>
        </div>
            <!-- requisition table ends -->

            <!-- requisition items sterts -->
             <!-- Button trigger modal -->


<!-- Modal -->
<div class="modal fade mymodal" id="requisitionItems" data-bs-backdrop="false" data-bs-keyboard="false" tabindex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
  <div class="modal-dialog modal-fullscreen">
    <div class="modal-content">
      <div class="modal-header">
        <h1 class="modal-title fs-5" id="staticBackdropLabel">Requisition Items </h1>
        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close" @click="clearForm"></button>
      </div>
      <div class="modal-body">
          <div class="row layout-top-spacing">
            <div class="col-xl-6 col-lg-12 col-sm-12 layout-spacing">
                <div class="panel p-0">
                    <div class="custom-table table3">
                        <v-client-table :data="requisitionItems" :columns="itemColumns" :options="table_option" >
                        </v-client-table>
                    </div>
                </div>
                 <div v-if="remarks.length>0">
                    <h4 class="text-center text-danger">Rejection Details </h4>
<table class="table table-bordered table-striped align-middle">
  <thead >
    <tr>
    <th>Sno</th>
      <th>Rejected By</th>
      <th>Date</th>
      <th>Reason</th>
    </tr>
  </thead>
  <tbody>
    <tr v-for="remark,i in remarks" :key="remark.id">
        <td>{{ i + 1 }}</td>
      <td>Director</td>
      <td>{{ new Date(remark.created_at).toLocaleDateString('en-GB') }}</td>
      <td>{{ remark.remark }}</td>
    </tr>
  </tbody>
</table>




                 </div>
            </div>


                    <!-- proforma starts -->
                      <div class="col-xl-6 col-lg-12 col-sm-12 layout-spacing">
                <div class="panel p-3"
                v-if="proforma"
                >
                <h4 class="text-center">Proforma Invoice</h4>
                     <img  :src="proforma" alt="Proforma Image" class="img-fluid" />
                </div>
            </div>
          
         <!-- proforma ends -->
        </div>


         
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-secondary" data-bs-dismiss="modal"
        @click="clearForm"
        >Close</button>
      </div>
    </div>
  </div>
</div>
             <!-- requisition items ends -->



   


    </div>
</template>

<script setup>
    import Multiselect from '@suadelabs/vue3-multiselect';
    import '@suadelabs/vue3-multiselect/dist/vue3-multiselect.css';
    import { onMounted, ref,inject,computed,watch,reactive } from 'vue';
    import axios from 'axios';
    import { useMeta } from '@/composables/use-meta';
    import '@/assets/sass/scrollspyNav.scss';
    import { useRoute } from 'vue-router';

    useMeta({ title: 'Requisition By Type' });

    const axiosInstance = axios.create({
        baseURL: process.env.VUE_APP_API_BASE_URL
    });

    const requisitionTypes = ref([]);
    const route = useRoute();
    const requisition_type = ref(route.params.type);
    const loadingIndicator = inject('loadingIndicator');
    const formatNumber = inject('formatNumber');
    const date = ref('')
    const asset_required = ref(false)
    const item = ref('')
    const description = ref('')
    const quantity = ref('')
    const rate = ref('')
    const remark = ref()
    const remarks = ref([])
    const item_id = ref(0)
    const specification = ref('')
    const required_date = ref('');
    const requisitions = ref([]);
    const loading_spinner = ref(false);
    const columns = ref(['Sno', 'requester','requisition_type','date','required_date','remark','status',  'actions']);
    const requisition_creation = ref(true);
    const showMessage = inject('showMessage');
    const showError = inject('showError');
    const formRequisitionItems = ref([])
    const formRequisitionItems2 = ref([])
    const upload = ref('')
const minRequiredDate = computed(() => {
  const base = date.value ? new Date(date.value) : new Date()
  const next = new Date(base)
  next.setDate(next.getDate() + 2)
  return next.toISOString().split('T')[0]
})

const enforceMax = () => {
      if (quantity.value > max_quantity.value) {
        quantity.value = max_quantity.value
        showError("only "+ quantity.value + " available")
      }

       if ((quantity.value * rate.value) > max_cost.value) {
        rate.value = ''
        showError("Cost should not exceed "+ formatNumber(max_cost.value) )
      }
    }

    
const unitList = ref([])
const getUnits = async ()=>{
  const response = await axiosInstance.get('units')
  unitList.value = response.data
}

const vendors = ref([])
const getVendors = async ()=>{
  const response = await axiosInstance.get('vendors')
  vendors.value = response.data
}
    const table_option = ref({
        perPage: 10,
        perPageValues: [ 10, 20, 30, 50],
        skin: 'table table-hover table-striped',
        columnsClasses: { actions: 'actions text-center' },
        sortable: ['id', 'role_name'],
        sortIcon: {
            base: 'sort-icon-none',
            up: 'sort-icon-asc',
            down: 'sort-icon-desc',
        },
        pagination: { nav: 'scroll', chunk: 5 },
        texts: {
            count: 'Showing {from} to {to} of {count}',
            filter: '',
            filterPlaceholder: 'Search...',
            limit: 'Results:',
        },
        resizableColumns: false,
    });

const handleFileUpload = (e) => {
  upload.value = e.target.files[0];
};


const assets = ref([])
const asset = ref('')
const getAssets = async () =>{
   const response = await axiosInstance.get('machines')
   assets.value = response.data
}




const i = ref(1)    
const clearItemForm = ()=>{
    quantity.value = '',
    rate.value = '',
    remark.value = '',
    item.value = ''
    description.value = ''
    units.value = ''
    asset.value = ''
    specification.value = ''
}


const clearSubmissionForm = () =>{
    requisition_type.value = '',
    date.value = '',
    required_date.value = '',
    quantity.value = '',
    rate.value = '',
    remark.value = '',
    item.value = ''
    formRequisitionItems.value = []
    formRequisitionItems2.value = []
    requisition_creation.value = false
}
    const pushItem = () => {
        if(!rate.value || !quantity.value ){
            showError("Fill in all fields");
            return ;
        }
    const payload = {
      Sno: i.value,
      item_id: item_id.value,
      quantity: quantity.value,
      rate: Number(rate.value).toLocaleString()+'/=',
      item: requisition_type.value === 'budget' ? item.value.name : item.value, 
    description: requisition_type.value === 'budget'
  ? description.value.name 
      + (asset.value ? " assigned to " + asset.value.category.name + " " + asset.value.name + " type " + asset.value.type_name : "")
      + " " + specification.value
  : description.value 
      + (asset.value ? " for " + asset.value.category.name + " " + asset.value.name + " type " + asset.value.type_name : "")
      + " " + specification.value,

     units: units.value?.short_name || units.value,

      total : Number(rate.value * quantity.value).toLocaleString()+'/=',
    };


    const payload2 = {
      Sno: i.value,
      item_id: item_id.value,
      quantity: quantity.value,
      rate: rate.value,
      remark: requisition_type.value === 'budget' ? item.value.name : item.value, 
description: requisition_type.value === 'budget'
  ? description.value.name 
      + (asset.value ? " assigned to " + asset.value.category.name + " " + asset.value.name + " type " + asset.value.type_name : "")
      + " " + specification.value
  : description.value 
      + (asset.value ? " for " + asset.value.category.name + " " + asset.value.name + " type " + asset.value.type_name : "")
      + " " + specification.value,
     units: units.value?.short_name || units.value,
      item_id : requisition_type.value === 'budget' ? description.value.id : 0, 
    };


    // Add Sno as index (1-based)
    i.value = i.value + 1;
    formRequisitionItems.value.push(payload);
    formRequisitionItems2.value.push(payload2);
    clearItemForm()
    }

    const pullItem = (sno) => {
        // Remove the item with the given Sno
        formRequisitionItems.value = formRequisitionItems.value.filter(item => item.Sno !== sno);
        // Rearrange the Sno for all items
        formRequisitionItems.value.forEach((item, idx) => {
            item.Sno = idx + 1;
            i.value = item.Sno + 1
        });
    }

    const requisitionItems = ref([])
    const itemColumns = ref(['Sno','item','description', 'units','quantity','rate','total']);
    const formItemColumns = ref(['Sno','item','description', 'units','quantity','rate','total','actions']);


    const clearForm = () =>{
      requisitionItems.value= ([])
    }

    const proforma = ref('')
    const fillItems = (req) => {
    remarks.value = req.remarks
    proforma.value = req.attachment
    requisitionItems.value = req.items.map((item, index) => ({
        ...item,
        Sno: index + 1,
        item: item.remark,
        // rate: Number(item.rate).toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 }) + '/=',
        rate: Number(item.rate).toLocaleString()+'/=',
        total : (item.quantity * item.rate).toLocaleString()+'/='
    }));

    }


const requisition_item_id = ref(route.params.type_id)
const loading = ref(false)
const vendor = ref('')
const registerRequisition = async () => {
  loading.value = true;  
  const formData = new FormData();

  formData.append('requisition', JSON.stringify({
    date: date.value,
    subcontractor_agreement_id: selected_subcontractor.value.id,
    vendor_id : vendor.value.id,
    required_date: required_date.value,
    requester_id: JSON.parse(localStorage.getItem("user")).id,
  }));

  formData.append('type', JSON.stringify({
    name: requisition_type.value,
    item_id: requisition_item_id.value,
  }));

  formData.append('items', JSON.stringify(formRequisitionItems2.value));

  if (upload.value) {
    formData.append('image', upload.value);
  }

  try {
    const response = await axiosInstance.post('create-requisition', formData, {
      headers: { 'Content-Type': 'multipart/form-data' }
    });

    showMessage(response.data.message);
    loading.value = false
    clearSubmissionForm()
    getRequisitions();

  } catch (error) {
    showError(error.message);
    loading.value = false
    console.log(error);
  }
};


  watch(item, (newVal) => {
        if (newVal) {
           budgetItemDescriptions.value = newVal.descriptions
        }
    });

const units = ref('')
const max_quantity = ref(0)
const max_cost = ref(0)
const available_rate = ref(0)
const budgetItems = ref([])
const budgetItemDescriptions = ref([])
const clearConstants = () =>{
  max_cost.value = 0,
  max_quantity.value = 0,
  available_rate.value = 0
  description.value = ''
}
if (requisition_type.value === 'budget') {
    watch(item, (newVal) => {
      if(newVal){
       clearConstants ()
      }
    })
  watch(description, (newVal) => {
    if (newVal) {
      units.value = newVal.units ?? "nill",
      specification.value = newVal.specification ?? ''
      max_quantity.value = newVal.quantity * newVal.quantity_2,
      max_cost.value = (newVal.quantity * newVal.quantity_2 * newVal.cost) - newVal.used_cost
      available_rate.value = newVal.cost
    }
  })
}



const getBudgets = async () => {
  try {
    const response = await axiosInstance.get(`budgets/${route.params.type_id}`)
    budgetItems.value = response.data?.items
  } catch (error) {
    showError(error.message)
    console.log(error)
  }
}


    const getRequisitions = () => {
        axiosInstance.get('requisitions')
            .then((response) => {
                requisitions.value = response.data.map((item, index) => {
                    const type = requisitionTypes.value.find(t => t.id === item.requisition_type_id);
                    return {
                        ...item,
                        Sno: index + 1,
                        requester_email: item.requester_email,
                        requisition_type: item.type,
                        attachment: item.attachment ? 'https://copex.trustdigital.space/' + item.attachment : null,
                        status_id : item.status.id,
                        status: {
                            key: item.status,
                            class: item.status_color
                        },
                    };
                });
                loading_spinner.value = false;
            })
            .catch((error) => {
                showError(error.message);
                loading_spinner.value = false;
            });
    };

const selected_subcontractor = ref('')
const subcontractor =  ref(false)
const subcontractors= ref([])
const getSubcontractors = async ()=>{
  const response = await axiosInstance.get('subcontractor-agreements')
  subcontractors.value = response.data.map((item)=>({
    ...item,
    name : item.project.short_name + ' - ' +item.item
  }))
}


    onMounted(async () => {
        getRequisitions();
        if(requisition_type.value === 'budget'){
         getBudgets()
        }

        getAssets()
        getUnits()
        getVendors()
        getSubcontractors()
